window.AudioContext = window.AudioContext || window.webkitAudioContext;

import sampleAuidoSrc from "../sounds/file_example_OOG_1MG.ogg";

const $ = document.querySelector.bind(document)


let audioContext: AudioContext | null = null;
var isPlaying: boolean = false;
var sourceNode: AudioBufferSourceNode | OscillatorNode | null = null;
let analyser: AnalyserNode | null = null;
let theBuffer: AudioBuffer | null = null;
let DEBUGCANVAS: HTMLCanvasElement;
var mediaStreamSource: MediaStreamAudioSourceNode | null = null;
let detectorElem: HTMLDivElement | null;
let canvasElem: HTMLElement | null;
let waveCanvas: CanvasRenderingContext2D | null;
let pitchElem: HTMLDivElement | null;
let noteElem: HTMLDivElement | null;
let detuneElem: HTMLDivElement | null;
let detuneAmount: HTMLDivElement | null;

let MAX_SIZE: number;
let isLiveInput: boolean;
window.onload = function () {
	audioContext = new AudioContext();
	MAX_SIZE = Math.max(4, Math.floor(audioContext.sampleRate / 5000));	// corresponds to a 5kHz signal
	let request = new XMLHttpRequest();
	request.open("GET", sampleAuidoSrc, true);
	request.responseType = "arraybuffer";
	request.onload = function () {
		audioContext!.decodeAudioData(request.response, function (buffer) {
			theBuffer = buffer;
		});
	}
	request.send();

	detectorElem = document.getElementById("detector") as HTMLDivElement;
	canvasElem = document.getElementById("output");
	DEBUGCANVAS = document.getElementById("waveform") as HTMLCanvasElement;
	if (DEBUGCANVAS) {
		waveCanvas = DEBUGCANVAS.getContext("2d");
		waveCanvas!.strokeStyle = "black";
		waveCanvas!.lineWidth = 1;
	}
	pitchElem = document.getElementById("pitch") as HTMLDivElement;
	noteElem = document.getElementById("note") as HTMLDivElement;
	detuneElem = document.getElementById("detune") as HTMLDivElement;
	detuneAmount = document.getElementById("detune_amt") as HTMLDivElement;

	detectorElem.ondragenter = function () {
		(this as HTMLDivElement).classList.add("droptarget");
		return false;
	};
	detectorElem.ondragleave = function () {
		(this as HTMLDivElement).classList.remove("droptarget"); return false;
	};
	detectorElem.ondrop = function (e) {
		(this as HTMLDivElement).classList.remove("droptarget");
		e.preventDefault();
		theBuffer = null;

		var reader = new FileReader();
		reader.onload = function (event) {
			audioContext!.decodeAudioData(event.target!.result as ArrayBuffer, function (buffer) {
				theBuffer = buffer;
			}, function () { alert("error loading!"); });

		};
		reader.onerror = function (event) {
			alert("Error: " + reader.error);
		};
		reader.readAsArrayBuffer(e.dataTransfer!.files[0]);
		return false;
	};



}

function error() {
	alert('Stream generation failed.');
}

function getUserMedia(dictionary: MediaStreamConstraints, callback: NavigatorUserMediaSuccessCallback) {
	try {
		// @ts-ignore
		navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
		navigator.getUserMedia(dictionary, callback, error);
	} catch (e) {
		alert('getUserMedia threw exception :' + e);
	}
}

function gotStream(stream: MediaStream) {
	// Create an AudioNode from the stream.
	mediaStreamSource = audioContext!.createMediaStreamSource(stream);

	// Connect it to the destination.
	analyser = audioContext!.createAnalyser();
	analyser.fftSize = 2048;
	mediaStreamSource.connect(analyser);
	updatePitch();
}

function toggleOscillator() {
	if (isPlaying) {
		//stop playing and return
		(sourceNode as OscillatorNode).stop(0);
		sourceNode = null;
		analyser = null;
		isPlaying = false;
		if (!window.cancelAnimationFrame)
			window.cancelAnimationFrame = window.webkitCancelAnimationFrame;
		window.cancelAnimationFrame(rafID);
		return "play oscillator";
	}
	sourceNode = audioContext!.createOscillator();
	analyser = audioContext!.createAnalyser();
	analyser.fftSize = 4096;
	sourceNode.connect(analyser);
	analyser.connect(audioContext!.destination);
	sourceNode.start(0);
	isPlaying = true;
	isLiveInput = false;
	updatePitch();

	return "stop";
}

function toggleLiveInput() {
	if (isPlaying) {
		//stop playing and return
		(sourceNode as OscillatorNode)!.stop(0);
		sourceNode = null;
		analyser = null;
		isPlaying = false;
		if (!window.cancelAnimationFrame)
			window.cancelAnimationFrame = window.webkitCancelAnimationFrame;
		window.cancelAnimationFrame(rafID);
	}
	getUserMedia(
		{
			audio: {
				mandatory: {
					"googEchoCancellation": "false",
					"googAutoGainControl": "false",
					"googNoiseSuppression": "false",
					"googHighpassFilter": "false"
				},
			},
		}, gotStream);
}


function togglePlayback() {
	if (isPlaying) {
		//stop playing and return
		sourceNode!.stop(0);
		sourceNode = null;
		analyser = null;
		isPlaying = false;
		if (!window.cancelAnimationFrame)
			window.cancelAnimationFrame = window.webkitCancelAnimationFrame;
		window.cancelAnimationFrame(rafID);
		return "start";
	}

	sourceNode = audioContext!.createBufferSource();
	sourceNode.buffer = theBuffer;
	sourceNode.loop = true;

	analyser = audioContext!.createAnalyser();
	analyser.fftSize = 2048;
	sourceNode.connect(analyser);
	analyser.connect(audioContext!.destination);
	sourceNode.start(0);
	isPlaying = true;
	isLiveInput = false;
	updatePitch();

	return "stop";
}

$('#useDemo')!.addEventListener('click', togglePlayback)
$('#useLive')!.addEventListener('click', toggleLiveInput)
$('#useOscill')!.addEventListener('click', toggleOscillator)


let rafID: number;

let buflen: number = 1024;
let buf: Float32Array = new Float32Array(buflen);

const noteStrings = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];

function noteFromPitch(frequency: number) {
	var noteNum = 12 * (Math.log(frequency / 440) / Math.log(2));
	return Math.round(noteNum) + 69;
}

function frequencyFromNoteNumber(note: number) {
	return 440 * Math.pow(2, (note - 69) / 12);
}

function centsOffFromPitch(frequency: number, note: number) {
	return Math.floor(1200 * Math.log(frequency / frequencyFromNoteNumber(note)) / Math.log(2));
}

